import React from 'react';
import { Link } from 'react-router-dom';

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faEllipsisH, faTimes } from '@fortawesome/free-solid-svg-icons';
import classes from './PrimaryNav.module.css';

const primaryNav = (props) => {
	const viewportWidth = window.innerWidth || document.documentElement.clientWidth;
	
	let button = <button className={classes.PrimaryNavBtn} onClick={props.showBtnClickedHandler}><h1>
		<FontAwesomeIcon icon={faEllipsisH}/></h1></button>;
	if (props.showSecondaryNav && viewportWidth <= 768) {
		button =
			<button className={classes.PrimaryNavBtn} onClick={props.closeBtnClickedHandler}><h1><FontAwesomeIcon
				icon={faTimes}/></h1></button>
	}
	
	return (
		<nav className={classes.PrimaryNav}>
			<Link className={classes.PrimaryNavLink} to="/"><h1>Refractor</h1></Link>
			{button}
		</nav>
	);
};

export default primaryNav;