import React from 'react';
import { Link } from 'react-router-dom';

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faArrowRight, faTimes } from '@fortawesome/free-solid-svg-icons';
import classes from './SecondaryNav.module.css';
import classNames from 'classnames/bind';

let cx = classNames.bind(classes);

const secondaryNav = (props) => {
	let className = cx({
		SecondaryNav: true,
		'SecondaryNav--show': props.showSecondaryNav
	});
	
	return (
		<nav className={className}>
			<div className={classes.SecondaryNavLinkContainer}>
				<Link className={[classes.SecondaryNavLink, classes.HasComma].join(' ')} to="/stories"><h1><FontAwesomeIcon className={classes.SecondaryNavLinkIcon} icon={faArrowRight} />Stories</h1></Link>
				<Link className={[classes.SecondaryNavLink, classes.HasComma].join(' ')} to="/about"><h1><FontAwesomeIcon className={classes.SecondaryNavLinkIcon} icon={faArrowRight} />About</h1></Link>
				<Link className={classes.SecondaryNavLink} to="/contact"><h1><FontAwesomeIcon className={classes.SecondaryNavLinkIcon} icon={faArrowRight} />Contact</h1></Link>
				<button className={classes.SecondaryNavCloseBtn} onClick={props.closeBtnClickedHandler}><h1><FontAwesomeIcon icon={faTimes} /></h1></button>
			</div>
		</nav>
	);
};

export default secondaryNav;